.page-body {
  font-family: var(--pt-sans);
  background-color: var(--basic-white);
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 1200px) {
  .page-body {
    max-width: 1200px;
  }
}
.main-header {
  height: 445px;
  background-image: url("../img/sedona-main-mobile.png");
  background-repeat: no-repeat;
  background-position: 50% 100%;
  background-size: cover;
}
@media (min-width: 768px) {
  .main-header {
    background-image: url("../img/sedona-main-tablet.png");
    height: 574px;
  }
}
@media (min-width: 1200px) {
  .main-header {
    background-image: url("../img/sedona-main-dekstop.png");
  }
}
.main-header__logo {
  display: block;
  width: 185px;
  height: 96px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 3;
  }
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    height: 56px;
    background-color: var(--basic-white);
  }
}
@media (max-width: 767px) {
  .site-list {
    display: none;
    background-color: var(--basic-blue);
  }
  .site-list__item {
    list-style: none;
    padding-top: 17px;
    padding-bottom: 12px;
    text-align: center;
  }
  .site-list__item:not(:last-child) {
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
  }
  .site-list__item:last-child {
    position: relative;
  }
  .site-list__item:last-child::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 7px;
    left: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (max-width: 767px) {
  .site-list__item:hover {
    background-color: var(--basic-blue-hover);
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
  .site-list__item:active {
    background-color: var(--basic-blue-active);
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
    opacity: 0.3;
    color: var(--basic-black);
  }
  .site-list__link {
    text-decoration: none;
    color: var(--basic-white);
    font-size: var(--size-M);
    line-height: var(--line-height-M);
    font-weight: var(--font-weight-bold);
    text-transform: uppercase;
  }
}
@media (min-width: 768px) {
  .site-list__item--logo-modifier {
    padding-left: 205px;
  }
}
@media (min-width: 1200px) {
  .site-list__item--logo-modifier {
    padding-left: 379px;
  }
}
@media (min-width: 768px) {
  .site-list__link {
    color: var(--basic-black);
    font-size: var(--size-S);
    line-height: var(--line-height-S);
    font-weight: var(--font-weight-bold);
    text-transform: uppercase;
    text-decoration: none;
  }
  .site-list__link:hover {
    color: var(--basic-blue);
  }
  .site-list__link:active {
    color: var(--basic-black);
    opacity: 0.3;
  }
  .site-list__link:focus {
    color: #766357;
  }
}
.advantage {
  background-color: var(--basic-white);
}
.advantage__header-wrapper {
  margin: 32px auto 16px;
  width: 282px;
}
@media (min-width: 768px) {
  .advantage__header-wrapper {
    margin: 47px auto 56px;
    width: 520px;
  }
}
@media (min-width: 1200px) {
  .advantage__header-wrapper {
    margin: 59px auto 45px;
    width: 483px;
  }
}
.advantage__header-title {
  font-size: var(--size-M);
  line-height: var(--line-height-M);
  font-weight: var(--font-weight-bold);
  text-transform: uppercase;
  text-align: center;
  margin: 0 auto 35px;
}
.advantage__header-intro {
  font-size: var(--size-S);
  line-height: var(--line-height-S);
  text-transform: uppercase;
  text-align: center;
  margin: 0 auto;
}
.advantage__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .advantage__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .advantage-item__first {
    grid-column-start: 1;
    grid-column-end: 4;
  }
  .advantage-item--main {
    display: flex;
    width: 100%;
  }
  .advantage-tips-list {
    display: flex;
  }
  .advantage-item__second {
    display: flex;
    grid-column-start: 1;
    grid-column-end: 4;
  }
}
.advantage-item {
  background-color: var(--basic-grey);
  box-sizing: border-box;
  min-height: 167px;
}
@media (min-width: 768px) {
  .advantage-item {
    min-height: 256px;
  }
}
.advantage-item--main {
  background-color: var(--basic-blue);
}
.advantage-item__wrapper {
  padding-top: 24px;
  padding-bottom: 24px;
  margin-left: 22px;
  margin-right: 22px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.advantage-item__title {
  font-size: var(--size-M);
  line-height: var(--line-height-M);
  font-weight: var(--font-weight-bold);
  color: var(--basic-black);
  text-transform: uppercase;
}
.advantage-item__title--main {
  color: var(--basic-white);
}
.advantage-item__list {
  display: block;
  position: relative;
  margin: 11px auto 17px;
}
.advantage-item__list--main {
  color: var(--basic-white);
}
.advantage-item__list--main::before {
  display: block;
  content: "";
  position: absolute;
  width: 8px;
  height: 1px;
  background-color: var(--basic-white);
  top: 50%;
  transform: translateY(-50%);
  left: -12px;
}
.advantage-item__list--main::after {
  display: block;
  content: "";
  position: absolute;
  width: 8px;
  height: 1px;
  background-color: var(--basic-white);
  top: 50%;
  transform: translateY(-50%);
  right: -12px;
}
.advantage-item__list--secondary {
  color: var(--basic-black);
}
.advantage-item__list--secondary::before {
  display: block;
  content: "";
  position: absolute;
  width: 8px;
  height: 1px;
  background-color: var(--basic-black);
  top: 50%;
  transform: translateY(-50%);
  left: -12px;
}
.advantage-item__list--secondary::after {
  display: block;
  content: "";
  position: absolute;
  width: 8px;
  height: 1px;
  background-color: var(--basic-black);
  top: 50%;
  transform: translateY(-50%);
  right: -12px;
}
.advantage-item__description {
  font-size: var(--size-S);
  line-height: var(--line-height-S);
  text-transform: uppercase;
  color: var(--basic-black-light);
}
.advantage-item__description--main {
  color: var(--basic-white);
}
.advantage-item__image--city {
  background-image: url("../img/photo-city.mobile.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 260px;
}
@media (min-width: 768px) {
  .advantage-item__image--city {
    background-image: url("../img/photo-city-tablet.png");
    width: 100%;
    height: 256px;
  }
}
@media (min-width: 1200px) {
  .advantage-item__image--city {
    background-image: url("../img/photo-city-desktop.png");
    width: 100%;
    height: 256px;
  }
}
.advantage-tips-list {
  background-color: var(--basic-white);
  padding: 0;
}
.advantage-tips-list__item {
  padding-top: 30px;
  padding-left: 65px;
  position: relative;
}
.advantage-tips-list__item:nth-child(1)::before {
  position: absolute;
  content: "";
  background-image: url("../img/icon-house-mobile.svg");
  top: 25px;
  left: 18px;
  width: 39px;
  height: 37px;
}
.advantage-tips-list__item:nth-child(2)::before {
  position: absolute;
  content: "";
  background-image: url("../img/icon-burger-mobile.svg");
  top: 25px;
  left: 18px;
  width: 40px;
  height: 35px;
}
.advantage-tips-list__item:nth-child(3)::before {
  position: absolute;
  content: "";
  background-image: url("../img/icon-gift-mobile.svg");
  top: 25px;
  left: 18px;
  width: 32px;
  height: 38px;
}
@media (min-width: 768px) {
  .advantage-tips-list__item {
    display: flex;
    flex-direction: column;
    padding-top: 158px;
    padding-left: 0;
    padding-bottom: 59px;
    text-align: center;
    width: calc(100% / 3);
  }
  .advantage-tips-list__item:nth-child(1)::before {
    position: absolute;
    content: "";
    background-image: url("../img/icon-house-tablet-desktop.svg");
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
    width: 75px;
    height: 72px;
  }
  .advantage-tips-list__item:nth-child(2)::before {
    position: absolute;
    content: "";
    background-image: url("../img/icon-burger-tablet-desktop.svg");
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
    width: 76px;
    height: 68px;
  }
  .advantage-tips-list__item:nth-child(3)::before {
    position: absolute;
    content: "";
    background-image: url("../img/icon-gift-tablet-desktop.svg");
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
    width: 64px;
    height: 75px;
  }
}
.advantage-tips-list__item-title {
  font-size: var(--size-M);
  line-height: var(--line-height-M);
  font-weight: var(--font-weight-bold);
  text-transform: uppercase;
}
.advantage-tips-list__item-text {
  font-size: var(--size-S);
  line-height: var(--line-height-S);
  color: var(--basic-black-light);
  margin: 0;
  padding-top: 16px;
  text-transform: uppercase;
}
.advantage-item__image--bridge {
  background-image: url("../img/photo-bridge-mobile.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 260px;
}
@media (min-width: 768px) {
  .advantage-item__image--bridge {
    background-image: url("../img/photo-bridge-tablet.png");
    height: 256px;
  }
}
@media (min-width: 1200px) {
  .advantage-item__image--bridge {
    background-image: url("../img/photo-bridge-desktop.png");
    width: 100%;
    height: 256px;
  }
}
:root {
  --basic-black: #000000;
  --basic-black-light: #333333;
  --basic-white: #ffffff;
  --basic-blue: #81b3d2;
  --basic-blue-hover: #669ec0;
  --basic-blue-active: #5496bd;
  --basic-grey: #eeeeee;
  --basic-grey-light: #f2f2f2;
  --basic-grey-opacity: rgba (0, 0, 0, 0.3);
  --basik-grey-substrate: #e7e5e5;
  --basik-grey-line-decoration: #dbdbdb;
  --basik-grey-active: #d5d5d5;
  --pt-sans: "PT Sans";
  --size-S: 14px;
  --size-M: 18px;
  --size-L: 21px;
  --line-height-S: 21px;
  --line-height-M: 26px;
  --font-weight-bold: bold;
  --margin-center: 0 auto;
}
