  z-index: 2;
}
.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  border: none;
  display: block;
  cursor: pointer;
  background-color: transparent;
  z-index: 3;
